@import url('https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@400;700&display=swap');

.darker_font{
    font-family: 'Darker Grotesque', sans-serif;
}
.input-field {
    border: 1px solid transparent; /* Ensure there's a border to change color on focus */
}

.input-field:focus {
    border-color: lightgray; /* Change to your desired border color */
    outline: none; /* Optional: Removes the default focus outline */
}
.chat_header{
    height: 4rem;
    background-color: white;
    display: flex;
    padding: 0.5rem;
   align-items: center;
}
.header_image{
    height: 100%;
}

.text_block_outer{
    margin-top: 1rem;
   
}
.message_author{
    color: gray;
    margin-bottom: 0.25rem;
    font-size: smaller;
}
.chat_area{
    width: 400px;
    display: none;
    position: fixed;
    right: 24px;
    bottom: 120px;
    border-radius: 1rem;
    margin: auto;
    background-color: white
}
.chat-visible{
    display: block;
}
.text_block_inner{
    background-color: white;
    padding: .5rem;
    border-radius: .5rem;
    text-align: left;
    display: inline-block;
    max-width: 80%;

    margin-right: 0%;
    box-shadow: rgba(0, 0, 0, 0.2) 2px 2px 9px;

}
.header_buttons{
    position: absolute;
    right: 15px;
    top: 15px;
    font-size: small;
    color: gray;
    cursor: pointer;
}

@media (max-width: 600px) {
    .chat_area {
      width: 95%;
      right: 0;
        position: relative;
      bottom: 0;
      left: 0;
        top: 0;


    }
    .inside_chat_parent{
        display: flex;
        height: 100%;
    }
  }